.adventures__color {
  height: 90px;
  background: linear-gradient(to bottom right, $color-white 50%, $color-light-gray 50%);
}

.adventuries__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  min-height: 245px;
  list-style-type: none;
  background-color: $color-light-gray;
}
.adventuries__item {

  @media (min-width: $breakpoint-large) {
    width: 252px;
    margin-right: 50px;
  }
}

.adventuries__item::last-child {
  @media (min-width: $breakpoint-large) {
    margin-right: 0;
  }
}

.adventuries__item--third {
  position: relative;
}

.adventuries__image--first,
.adventuries__image--second {
  display: none;
  @media (min-width: $breakpoint-large) {
    display: block;
  }
}

.adventuries__image--third {
  @media (min-width: $breakpoint-medium) {
    position: absolute;
    top: 0;
    left: -310px;
  }

  @media (min-width: $breakpoint-large) {
    position: static;
  }
}

.adventuries__title {
  font-size: 18px;
  line-height: 24px;
  @media (min-width: $breakpoint-large) {
    font-size: 30px;
    line-height: 30px;
  }
}

.adventuries__paragraph {
  font-size: 14px;
  line-height: 24px;
  @media (min-width: $breakpoint-large) {
    font-size: 18px;
    line-height: 30px;
  }
}

.adventuries__title {
  font-size: 18px;
  line-height: 24px;
}
.adventuries__paragraph {
  font-size: 14px;
  line-height: 24px;
}
